import React, { useEffect, useState, useRef } from 'react';
import ProjectHelper from './ProjectHelper';
import { useSelector, useDispatch } from 'react-redux';

const Project = ({ heading, data }) => {
   
    const [loaded, setLoaded] = useState(false);
    const [showHelper, setShowHelper] = useState(false);
    const image = useRef(null);
    const refreshIntervalId = useRef(null);
    const [hovered, setHovered] = useState(false);
    const close = ()=>{
        setShowHelper(false);
        setHovered(false);
    }
    useEffect(() => {

        if (image.current && !refreshIntervalId.current)
            refreshIntervalId.current = setInterval(() => {
                if (image.current) {
                    setLoaded(image.current.complete)
                }
            }, 100);
    })
    if (loaded) {
        clearInterval(refreshIntervalId.current);
    }

    return (<div>
        <section id='project' onMouseEnter={() => { setHovered(true) }}
            onMouseLeave={(e) => {
                setHovered(false);

            }}
            onClick = {()=>{setHovered(true)}}
            >
            <div className='img-container' style={loaded ? { opacity: 0 } : {}}>

            </div>
            <div className='img'
                style={{
                    background: data['fill'] ?
                        'radial-gradient(' + data["fill"] + ',transparent)' : 'linear-gradient(45deg,white,#efefef)',
                    filter: hovered ? 'blur(20px)' : ''
                }}>
                <img ref={image} src={'/Screenshot/' + heading + '/heading.jpg'} style={{ filter: 'drop-shadow(8px 8px 10px gray)' }} />
            </div>
            <div className='text-heading' style={hovered ? { opacity: 1, top: '40%' } : {}}>
                {heading}
            </div>
            <div className='know-more' id='button' style={hovered ? { opacity: 1, bottom: '25%',pointerEvents:'all' } : {}}
                onClick={() => {
                    setShowHelper(true);
                }}
            >
                Learn more
            </div>
            
        </section>
        {showHelper ?
            <ProjectHelper data={data} close={close} heading={heading}/>
          : null}
          </div>
    )
}

export default Project;