import React from 'react';
import CustomBox from '../../components/CustomBox';
import  JS  from '../../assets/js.png';
import PS from '../../assets/problemSolving.png';
import REACT from '../../assets/react.png';

const Certifications = () => {
    
    const styles= {
        image:{
            padding:'1em',
            margin:'auto',
            cursor:'pointer',
            objectFit:'cover',
            width:'90%'
        }
    }

    return (
        <div style={{ color: 'white', fontFamily: 'montserrat', marginBottom: '5em',display:'flex',flexWrap:'wrap' }}>
            <CustomBox width={'35vw'}>
                <img src={JS} style={styles.image} onClick = {()=>{window.open('https://www.hackerrank.com/certificates/5483e55d504f')}}/>
            </CustomBox>
            <CustomBox width={'35vw'}>
                <img src={PS} style={styles.image} onClick = {()=>{window.open('https://www.hackerrank.com/certificates/6bd604fee8f9')}} />
            </CustomBox>
            <CustomBox width={'35vw'}>
                <img src={REACT} onClick = {()=>{window.open('https://www.hackerrank.com/certificates/fb4a0aecc0a7')}} style={styles.image}/>
            </CustomBox>
        </div>
    )
}

export default Certifications;