import React, { useState } from 'react';
import CustomBox from '../../components/CustomBox';
import ReactIcon from '../../assets/experience/React-icon.svg';
import JS from '../../assets/experience/JS.png';
import Spring from '../../assets/experience/SpringBoot.svg';
import CS from '../../assets/experience/CSS.png';
import JAVA from '../../assets/experience/java.svg';
import node from '../../assets/experience/node.svg';
import polymer from '../../assets/experience/polymer.svg';
import angular from '../../assets/experience/angular.svg';

const data = {
    React: {
        icon: ReactIcon,
        list: [
            'Redux', 'React-Router', 'SVG’s', 'Context-API', 'Hooks', 'State Management'
        ]
    },
    'Java Script': {
        icon: JS,
        list: [
            'JQuery', 'Await/Async', 'ES6', 'loadash', 'Timers', 'webGL', 'Promise', 'Lazy-Load'
        ]
    },
    'Spring boot': {
        icon: Spring,
        list: [
            'Spring web', 'Hybernate', 'JSON', 'Soap', 'Aptuator', 'Eureka', 'Mockito', 'Maven', 'JWT Token', 'Microservices', 'REST', 'File upload', 'FileDownload'
        ]
    },
    'HTML CSS': {
        icon: CS,
        list: ['Responsive Design', 'Lazy Load', 'Flexbox', 'Grid', 'Transition', 'filter', 'keyframes', 'Transform', 'Canvas', 'webGL']
    },
    'Java': {
        icon: JAVA,
        list: ['Thread', 'Swing', 'Sockets', 'Class', 'Collections', 'Interface', 'OOP']
    },
    'React Native': {
        icon: ReactIcon,
        list: [
            'Expo', 'Context', 'State Management', 'Hooks', 'react-native-svg-animations'
        ]
    },
    'Angular': {
        icon: angular,
        list: [
            'Basic App Creation'
        ]
    },
    'Node': {
        icon: node,
        list: [
            'Basic Level Understanding','package structure'
        ]
    },
    'Polymer': {
        icon: polymer,
        list: [
            'basic App Creation','Subscriber/publish','basic app creation'
        ]
    },
};

const SkillSet = () => {

    const styles = {
        main: {
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            textAlign: 'center',
            padding: '1em',
            alignItems: 'center'
        },
        technology: {
            width: '250px',
            marginBottom: '1.5em',
            display: 'flex',
            flexDirection: 'column'
        },
        rest: {
            flex: 1,
            display: 'none',
            flexWrap: 'wrap',
            alignItems: 'center'
        },
        item: {
            width: '200px',
            height: '3em',
        },
        image: {
            width: '180px',
            margin: 'auto',
            marginBottom: '0.75em',
        },
        imageB: {
            width: '100px',
            margin: 'auto',
            marginBottom: '0.75em',
        }
    }

    const [state, setState] = useState(data);
    let op = [];

    Object.keys(state).forEach((key) => {
        let WIDTH = '250px';

        if (state[key]['active']) {
            WIDTH = '65vw';
        }
        op.push(
            <div style={{ marginLeft: '0.75em', marginRight: '0.75em' }} id={key + 'skill'} key={key} >
                <CustomBox key={key} width={WIDTH} >
                    <div style={{ position: 'absolute', top: '0.25em', right: '1em', cursor: 'pointer', fontSize: '1.5em' }}
                        onClick={() => {
                            let updatedState = { ...state };
                            updatedState[key]['active'] = updatedState[key]['active'] ? !updatedState[key]['active'] : true;

                            setState(updatedState);
                            setTimeout(()=>{
                                    let height = document.getElementById(key + 'skill').offsetTop ;
                                    window.scrollTo({ top: height-80, behavior: 'smooth' });
                            },50);
                           
                        }}
                    >
                        {state[key]['active'] ? ' X ' : ' + '}
                    </div>
                    <div style={styles.main}>
                        <div style={styles.technology}>
                            <img src={state[key]['icon']} style={styles.image}></img>
                            {key}
                        </div>
                        <div style={{ ...styles.rest, display: state[key]['active'] ? 'flex' : 'none' }} >
                            {state[key]['list'].map((entry, id) => (
                                <li key={id} style={styles.item}>{entry}</li>
                            ))}
                        </div>
                    </div>
                </CustomBox>
            </div>);
    })

    return (
        <div>
            <div className='containerSkill' style={{ color: 'white', justifyContent: 'center', fontFamily: 'montserrat', margin: 'auto', display: 'flex', flexWrap: 'wrap' }}>
                {op}
            </div>
        </div>
    )
}

export default SkillSet;