import React,{useState,useEffect} from 'react';
import {CVNav,Certifications,Experience,Publications,SkillSet} from '../CVSection';
// import { useHistory } from "react-router-dom";
import { useLocation } from "react-router-dom";

const CVSection = () =>{
    
    const location = useLocation();
    
    const [active,setActive] = useState(0);
    useEffect(()=>{
        let screen = location.pathname.split("/")[2];
        switch(screen){
            case 'Publications':
                setActive(0);
                break;
            case 'Certifications':
                setActive(1);
                break;
            case 'Experience':
                setActive(2);
                break;
            case 'SkillSet':
                setActive(3);
                break;
        }
    },[location.pathname])
    return (
        <div style={{background:'#313131',minHeight:'100vh',paddingBottom:'1em'}}>

            <CVNav active={active}/>
            <div style={{marginTop:'8em'}}>

         
            {(active === 0)&&(
                <Publications/>
            )}
            {(active === 1)&&(
                <Certifications/>
            )}
            {(active === 2)&&(
                <Experience/>
            )}
            {(active === 3)&&(
                <SkillSet/>
            )}
               </div>
        </div>
    )

}

export default CVSection;