import React, { useEffect, useRef, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Animating, closeMenu, openMenu, setIndex } from '../redux/actions';
import { GmailIconGroup } from './Navigate';
import { ReactComponent as Home } from '../assets/icons/home.svg';
import { ReactComponent as Contact } from '../assets/icons/contact.svg';
import { ReactComponent as Projects } from '../assets/icons/projects.svg';
import { ReactComponent as About } from '../assets/icons/about.svg';

const Sidebar = () => {
    const [color, setColor] = useState(['white', 'black','white']);
    const wrapperRef = useRef(null);
    const dispatch = useDispatch();
    const { menuOpen, index, animating } = useSelector(state => state.navBarReducer);

    useEffect(() => {
        if (menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(-100px, 0px, -600px) rotateY(20deg)';
        if (!menuOpen)
            document.getElementsByClassName('awssld--active')[0].style['transform'] = 'translate3d(0,0,0)';

        function handleClickOutside(event) {
            if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
                dispatch(closeMenu());
            }
        }
        // function handleResize() {
        //     if (window.innerWidth <= 960) {
        //         setColor(['white', 'black', 'black']);
        //     } else {
        //         setColor(['white', 'black', 'white']);
        //     }
        // }

        // window.addEventListener('resize', handleResize)
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            // Unbind the event listener on clean up
            document.removeEventListener("mousedown", handleClickOutside);
            // document.removeEventListener("resize", handleResize);
        };
    }, [menuOpen, wrapperRef, dispatch])

    return (
        <div id='Sidebar' ref={wrapperRef}>
            <p>
            <span style={menuOpen ? { color: "white", cursor: "pointer" } : { color: color[index], cursor: "pointer" }} onClick={() => { dispatch(setIndex(0)) }}>
                Rishabh Chaudhary
            </span>
            </p>
            <div id='Hamburger' style={!animating ? {} : { pointerEvents: "none" }} onClick={() => {
                if (menuOpen) {
                    dispatch(closeMenu());
                } else {
                    dispatch(openMenu());
                }
            }} >
                <div id='MenuIcon'>
                    <div className='line' style={!menuOpen ? { background: color[index] } : { opacity: 0 }} />
                    <div className='line' style={!menuOpen ? { background: color[index] } : { transform: ' rotate(45deg)' }} />
                    <div className='line' style={!menuOpen ? { background: color[index] } : { transform: 'translateY(-10px) rotate(-45deg)' }} />
                </div>

            </div>
            <div id='Menu' style={menuOpen ? {} : { width: '0', overflow: 'hidden' }} >
                <div className={index === 0 ? 'item active' : 'item'} onClick={(e) => {
                    let time = 0;
                    if (index !== 0) {
                        let par = e.target.parentElement;
                        console.log(par.parentElement);
                        par.children[index].classList.toggle('active');
                        par.children[0].classList.toggle('active');
                        time = 400;
                        dispatch(Animating());
                    }
                    setTimeout(() => dispatch(setIndex(0)), time);
                }}>
                    <Home height={index === 0 ? '2.25em' : '2em'} stroke={index === 0 ? "white" : "#817889"} style={{ marginLeft: '-20%', pointerEvents: 'none' }} />

                    <div style={{ marginLeft: '20%', pointerEvents: 'none', fontSize: index === 0 ? '1.25em' : '1em' }}>
                        Home
                    </div>
                </div>
                <div className={index === 1 ? 'item active' : 'item'} onClick={(e) => {
                    let time = 0;
                    if (index !== 1) {
                        let par = e.target.parentElement;
                        par.children[index].classList.toggle('active');
                        par.children[1].classList.toggle('active');
                        time = 500;
                        dispatch(Animating());
                    }
                    setTimeout(() => dispatch(setIndex(1)), time);
                }}>
                    <Projects height={index === 1 ? '2.25em' : '2em'} stroke={index === 1 ? "white" : "#817889"} style={{ marginLeft: '-20%', pointerEvents: 'none' }} />
                    <div style={{ marginLeft: '20%', pointerEvents: 'none', fontSize: index === 1 ? '1.25em' : '1em' }}>
                        Projects
                    </div>
                </div>
                <div className={index === 2 ? 'item active' : 'item'} onClick={(e) => {
                    let time = 0;
                    if (index !== 2) {
                        let par = e.target.parentElement;
                        par.children[index].classList.toggle('active');
                        par.children[2].classList.toggle('active');
                        time = 500;
                        dispatch(Animating());
                    }
                    setTimeout(() => dispatch(setIndex(2)), time);
                }}>
                    <About height={index === 2 ? '2.25em' : '2em'} stroke={index === 2 ? "white" : "#817889"} style={{ marginLeft: '-20%', pointerEvents: 'none' }} />
                    <div style={{ marginLeft: '20%', pointerEvents: 'none', fontSize: index === 2 ? '1.25em' : '1em' }}>
                        About Me
                    </div>
                </div>
                <GmailIconGroup className='gmailIconsInMenu' />
            </div>
        </div>
    )
}

export default Sidebar;