import React from 'react';
import { ReactComponent as Codechef } from '../assets/icons/codechef.svg';
import { ReactComponent as Euler } from '../assets/icons/Euler.svg';
import { ReactComponent as Hackerrank } from '../assets/icons/hackerrank.svg';
import { ReactComponent as LeetCode } from '../assets/icons/LeetCode.svg';
import { onTouchMove, onTouchStart, onTouchEnd } from '../utils/touchHandler';
import {useDispatch} from 'react-redux';
import { useHistory } from "react-router-dom";

const About = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    return (
        <section id='about'
            onTouchStart={touchStart => onTouchStart(touchStart)}
            onTouchMove={touchMoveEvent => onTouchMove(touchMoveEvent)}
            onTouchEnd={() => { onTouchEnd(dispatch, 50) }}>
            <div className='about' style={{position:'sticky',top:'10vh'}}>

                <div className='myPic'>
                </div>
                <div style={{ flex: 1 }} className='myInfo'>
                    I am Rishabh Chaudhary a software engineer.
                    I love to learn new technologies by building small projects .
                    I also do competetive programming to improve problem solving in my free time .
                    Currently i am living in Hyderabad,India
                </div>
            </div>
            <div className='buttonContainer'>
                <div className='know-more' id='button' onClick={()=>{history.push("/cvsection/Publications")}}>
                    Publications
            </div>
                <div className='know-more' id='button' onClick={()=>{history.push("/cvsection/Certifications")}}>
                    Certifications
            </div>
                <div className='know-more' id='button' onClick={()=>{history.push("/cvsection/Experience")}}>
                    Experience
            </div>
                <div className='know-more' id='button' onClick={()=>{history.push("/cvsection/SkillSet")}}>
                    Skillset
            </div>
            </div>
            <div className='iconContainer'>
                <Codechef className='icon' onClick={() => { window.open("http://www.codechef.com/users/resda") }} />
                <Hackerrank className='icon' onClick={() => { window.open("https://www.hackerrank.com/rohanakar") }} />
                <LeetCode className='icon' onClick={() => { window.open("https://leetcode.com/rishabhc329/") }} />
                <Euler className='icon' onClick={() => { alert("Friend key 1330279_oo04DozPjd6GzZPclkM6IUbQEDEeIns4") }} />
            </div>

        </section>
    )
}

export default About;