Object.keys(projectsLite).forEach(key => {
    projectsLite[key].tags.push('1 day Projects');
})

const defaultState = {
    projects: { ...projects, ...projectsLite },
    filter: 0,
    filterElements: ['All', 'React', 'JavaScript', 'SpringBoot', 'Java', 'React-Native'],
}
const projectReducer = (state = defaultState, action) => {
    switch (action.type) {
        case 'setFilter':
            return { ...state, filter: action.payload };
        default:
            return state;
    }
}

export default projectReducer;