import React, { useState } from 'react';
import { ReactComponent as Left } from '../assets/icons/left.svg';
import { ReactComponent as Right } from '../assets/icons/right.svg';
import { ReactComponent as ExternalLink } from '../assets/icons/external-link.svg';
import { ReactComponent as Cross } from '../assets/icons/x.svg';
import { ReactComponent as File } from '../assets/icons/file.svg';


const ProjectHelper = ({ data, heading, close }) => {

    const [counter, setCounter] = useState(0);
    let ind = (data.imageCount + counter) % data.imageCount;
    let imageType = '.jpg';
    if (ind < data.gifCount)
        imageType = '.gif';
    let descInd = (counter*2+data.description.length)%data.description.length;
    return (
        <div className='projectHelper'>
            {/* {heading} */}
            <div className='gallery'>
                <div className='container' >
                    <img src={'/Screenshot/' + heading + '/' + ind + imageType} style={{ filter: 'drop-shadow(8px 8px 10px gray)' }} />
                </div>
                <Left className='left' onClick={() => { setCounter(counter - 1) }} />
                <Right className='right' onClick={() => { setCounter(counter + 1) }} />
            </div>
            <div className='body' style={{overflow:'scroll',height:'25%'}}>
                <div className='title'>
                    {heading}
                </div>
                <br/>
                {data.description[descInd]}<br/><br/>
                {data.description[descInd+1]}

            </div>
            <div className='bottom'>
                {data.live ?
                    <div className='know-more' id='button' onClick={() => { window.open(data.live) }}>
                        <ExternalLink />
                        <div style={{ paddingLeft: '0.75em' }}>Live Demo</div>
                    </div>
                    : null}
                {data.github ?
                    <div className='know-more' id='button' onClick={() => { window.open(data.github) }}>
                        <File />
                        <div style={{ paddingLeft: '0.75em' }}>Source Code</div>
                    </div> : null}


            </div>
            <div className='close' onClick={() => { close() }}>
                <Cross height={'1em'} />
            </div>

        </div>
    )
}

export default ProjectHelper;