import React from 'react';

const styles = {
    box: {
        borderRadius: '33px',
        background: '#313131',
        boxShadow: '6px 6px 13px #1c1c1c, -6px -6px 13px #464646',
        padding:'1em',
        margin:'auto',
        marginTop:'5em',
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        position:'relative',
        minHeight:'300px'
    }
}

const CustomBox = ({ children,width }) => {
    return (<div className='customBox' style={{...styles.box,width:width}}>{children}</div>);
}

export default CustomBox;