import React from 'react';
import CustomBox from '../../components/CustomBox';

const Publications = () => {
    return (
        <div style={{ color: 'white', fontFamily: 'montserrat',marginBottom:'5em',display:'flex',flexWrap:'wrap' }}>
            <CustomBox >
                <div style={{ padding: '1em',textAlign:'center' }}>
                    {'Conference Proceedings'}
                </div>
                <div style={{ padding: '1em' }}>
                    {'S. Moulik R. Chaudhary, Z. Das and A. Sarkar (2020). “EA-HRT: An Energy-Aware scheduler for Heterogeneous Real-Time systems”. In: IEEE/ACM ASP-DAC, Beijing, China'}
                </div>
            </CustomBox>
            <CustomBox>
                <div style={{ padding: '1em',textAlign:'center'}}>{'Journal Articles'}</div>
                <div style={{ padding: '1em' }}>{'Sanjay Moulik Rishabh Chaudhary, Zinea Das. “HEARS: A Heterogeneous Energy-Aware Real-time Scheduler”. In: Microprocessors and Microsystems, Elsevier(Accepted).'}</div>
            </CustomBox>
        </div>
    )
}

export default Publications;