import React from 'react';
import { useHistory } from "react-router-dom";

const CVNav = ({ active }) => {
    console.log(active);
    const history = useHistory();
    const styles = {
        cvnav: {
            background: '#0066B8',
            height: '3em',
            top: 0,
            left: 0,
            fontFamily: 'montserrat',
            fontWeight: 'bold',
            color: 'white',
        },
        home: {
            position: 'absolute',
            margin: 'auto',
            left: '1em',
            padding: '1em',
            display: 'inline',
            cursor: 'pointer'

        },
        cross: {
            position: 'absolute',
            right: '2em',
            display: 'inline',
            paddingTop: '1em',
            paddingBottom: '1em',
            margin: 'auto',
            cursor: 'pointer'
        },
        links: {
            display: 'flex',
            flexWrap: 'wrap',
            width: '50vw',
            margin: 'auto',
            background: '#494848',
            color: 'white',
            fontFamily: 'montserrat',
            fontWeight: 'bold',
            marginTop: '1em',
        },
        link: {
            padding: '1em',
            flex: 1,
            textAlign: 'center',
            cursor: 'pointer'
        },
        active: {
            background: 'black',
        }
    }
    return (
        <div >
            <div style={{
                position: 'fixed',
                width: '100%',
                top:0,
                background: 'rgb(49, 49, 49)',
                zIndex:1,
              
            }}>
                <div className='cvnav' style={styles.cvnav}>
                    <div className='Home' style={styles.home} onClick={() => { history.push("/") }}>
                        Rishabh Chaudhary
            </div>
                    <div className='cross' style={styles.cross} onClick={() => { history.push("/") }}>
                        X
            </div>
                </div>
                <div className='Links' style={styles.links}>
                    <div className='Link' style={active !== 0 ? styles.link : { ...styles.link, ...styles.active }}
                        onClick={() => { history.replace("/cvsection/Publications") }}
                    >Publications</div>
                    <div className='Link' style={active !== 1 ? styles.link : { ...styles.link, ...styles.active }} onClick={() => { history.replace("/cvsection/Certifications") }}> Certifications</div>
                    <div className='Link' style={active !== 2 ? styles.link : { ...styles.link, ...styles.active }} onClick={() => { history.replace("/cvsection/Experience") }}>Experience</div>
                    <div className='Link' style={active !== 3 ? styles.link : { ...styles.link, ...styles.active }} onClick={() => { history.replace("/cvsection/SkillSet") }} >Skills</div>
                </div>
            </div>
        </div>

    )
}

export default CVNav;